const state = {
    listUser: [],
    activatingUser: null,
    myProfile: null
}

const mutations = {
    UPDATE_LISTUSER(state, payload) {
        if (state.activatingUser !== null) {
            payload.forEach((user) => {
                if (user._id === state.activatingUser._id) {
                    state.activatingUser = user;
                }
            })
        }

        state.listUser = payload;
    },
    SET_ACTIVATING_USER(state, user) {
        state.activatingUser = user;
    },
    SET_MY_PROFILE(state, payload) {
        state.myProfile = payload;
    },
    UPDATE_USER(state, payload) {
        const {
            _id,
            lastMessage,
            unseen
        } = payload;
        for (let i = 0; i < state.listUser.length; i++) {
            if (state.listUser[i]._id === _id) {
                state.listUser[i].lastMessage = lastMessage;
                state.listUser[i].unseen = unseen;
            }
        }
    }
}


// Nhan emit tu phia server

const actions = {
    SOCKET_coNguoiOnline({
        commit
    }, listUser) {

        commit('UPDATE_LISTUSER', listUser);
    },
    SOCKET_coNguoiOffline({
        commit
    }, listUser) {
        commit('UPDATE_LISTUSER', listUser);
    },
    activeUser({
        commit
    }, userToActive) {
        commit('SET_ACTIVATING_USER', userToActive);
    },
    setMyProfile({
        commit
    }, payload) {
        commit('SET_MY_PROFILE', payload);
    },
    updateUser({
        commit
    }, payload) {
        commit('UPDATE_USER', payload);
    }
}

const getters = {
    listUser: state => {
        return state.listUser
    },
    activatingUser: state => state.activatingUser,
    myProfile: state => state.myProfile
}

const chatModule = {
    state,
    mutations,
    actions,
    getters
};

export default chatModule;