<template>
  <nav class="gray-1">
    <div class="nav__left">
      <router-link to="/" class="pt-3 pb-3 pl-4 pr-4">Kh1emCh4t</router-link>
    </div>
    <div class="nav__right">
      <router-link to="/profile" class="pt-3 pb-3 pl-4 pr-4">Profile</router-link>
      <router-link to="/chat" class="pt-3 pb-3 pl-4 pr-4">Chat</router-link>
      <a href="#" class="pt-3 pb-3 pl-4 pr-4" @click="logout">Log Out</a>
    </div>
  </nav>
</template>

<script>
import axios from "axios";
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["myProfile"]),
  },
  methods: {
    async logout() {
      const idUserOffline = this.myProfile._id;
      this.$socket.emit("taoOfflineNe", idUserOffline);
      await axios.post("/api/logout");
      this.$bus.emit("loggedout");
      this.$router.push("/");
    },
  },
};
</script>

