<template>
  <div id="app">
    <NavLogin />
    <router-view />
  </div>
</template>

<script>
import NavLogin from "./NavLogin";
import { mapGetters } from "vuex";
export default {
  name: "AppLoggedIn",
  components: {
    NavLogin,
  },
  created() {
    this.$bus.on("playSoundNewMessage", () => {});

    this.$store.dispatch("setMyProfile", this.decodePayload());
    this.$socket.emit("taoOnlineNe", this.myProfile._id);
  },
  computed: {
    ...mapGetters(["myProfile"]),
  },
  sockets: {
    coTinNhanMoi: function () {
      const audio = new Audio(require("../assets/juntos.mp3"));
      audio.volume = 0.3;
      audio.play();
    },
  },
};
</script>

<style>
body {
  height: 100vh;
}

#app {
  min-height: 100vh;
  max-height: 100vw;
}
</style>