const state = {
    isLoading: false
}


const mutations = {
    SET_LOADING(state, payload) {
        state.isLoading = payload;
    }
}


const actions = {
    setLoading({
        commit
    }, payload) {
        commit('SET_LOADING', payload);
    }
}

const getters = {
    isLoading: state => state.isLoading
}

const authModule = {
    state,
    mutations,
    actions,
    getters
};

export default authModule;