import axios from 'axios';

export default {

    install(Vue) {
        Vue.prototype.needAuthenticate = async function () {
            if (document.cookie) {
                const response = await axios.post('/api/auth', {}, {
                    headers: {
                        'X-Requested-With': 'XMLHttpRequest'
                    }
                });
                if (response.data.isError) {
                    await axios.post('/api/logout');
                    this.$router.push('/');
                }
            } else {
                this.$router.push('/');
            }
        };

        Vue.prototype.needVerify = async function () {
            if (document.cookie) {
                const response = await axios.post('/api/verify', {}, {
                    headers: {
                        'X-Requested-With': 'XMLHttpRequest'
                    }
                });
                if (response.data.isError) {
                    if (response.data.err === "authenticateFail") {
                        await axios.post('/api/logout');
                        this.$router.push('/');
                    } else if (response.data.err === "verifyFail") {
                        this.$router.push('/verification');
                    }
                }
            } else {
                this.$router.push('/');
            }
        }

        Vue.prototype.forwardAuthenticate = async function () {
            if (document.cookie) {
                this.$router.push('/profile');
            }
        }
        Vue.prototype.decodePayload = function () {
            let jwt_headerAndPayload;
            let i, x, y, ARRcookies = document.cookie.split(";");
            for (i = 0; i < ARRcookies.length; i++) {
                x = ARRcookies[i].substr(0, ARRcookies[i].indexOf("="));
                y = ARRcookies[i].substr(ARRcookies[i].indexOf("=") + 1);
                x = x.replace(/^\s+|\s+$/g, "");
                if (x == 'headerAndPayload') {
                    jwt_headerAndPayload = unescape(y);
                    break;
                }
            }

            const foundDot = jwt_headerAndPayload.indexOf('.');
            const buff = Buffer.from(jwt_headerAndPayload.substr(foundDot + 1), 'base64');
            let payload = buff.toString('ascii');
            payload = JSON.parse(payload);
            return payload;
        }
    }
}